@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background: linear-gradient(240deg, #2c3e50, #3498db, #8e44ad);
  font-family: 'Poppins', sans-serif;
  color: #fff;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
}

a {
  color: #fff;
  text-decoration: none;
}

.btn {
  background-color: #fff;
  color: #174f83;
  padding: 8px 12px;
  border-radius: 4px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

.btn:hover {
  background-color: #174f83;
  color: #fff;
}
