.app {
    position: relative;
    min-height: 100vh;
  }
  
  body {
    margin: 0;
  }
  
  .auth-button-container {
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    /* background-color: #f1f1f1; */
    box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.1);
  }
  
  .auth-button-container a {
    color: #333;
    text-decoration: none;
    font-weight: bold;
  }
  